import React from 'react'
import EventModule from './EventModule'
import parseISOString from './utility/parseISOString'

import './styles/EventList.css'

const EventList = (props) => {
  return (
    <div id="event-list">
      <h1>In The Works</h1>
      {props.events.map((event) => {
        return (
          <EventModule
            price={
              event.is_external ?
               null : `${event.min_ticket_price} - ${event.max_ticket_price}`}
            key={event.id}
            id={event.id}
            name={event.name}
            start={parseISOString(event.event_start).slice(0, 9)}
            time={parseISOString(event.event_start).slice(10, 19)}
            image={event.promo_image_url}
            url={event.external_url}
          />
        )
      })}
    </div>
  )
}

export default EventList



