import React, { Component } from 'react'
import { currentEvents } from './Events';
import EventList from './EventList'
import NewsletterSignUp from './NewsletterSignUp'
import sortEvents from './utility/sortEvents';

import './styles/InTheWorks.css'


class InTheWorks extends Component {
  constructor(props) {
    super(props);
    this.state = {
      events: [],
      images: [],
    }
  }

  componentDidMount() {
    currentEvents().then((eventIndex) => {
      this.setState({ events: sortEvents(eventIndex) });
    });
  }

  render() {
    return (
      <div id="in-the-works">
        <EventList events={this.state.events}/>
        <NewsletterSignUp />
      </div>
    )
  }
}

export default InTheWorks



